* {
  text-decoration: none;
  list-style-type: none;
}
#root {
  width: 100%;
  height: 100%;
}
body {
  background-image: url(../img/starsyss.gif);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  margin: 0;
  width: 100vw;
  height: 100vh;
}
.header {
  width: 100%;
  height: 20vh;

  &__img {
    width: 300px;
    height: 100px;
    margin: 30px;
  }
}
.main {
  width: 100%;
  height: 70vh;
  &__form {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: 'Gemunu Libre', sans-serif;
    font-size: large;
    &--container {
      width: 100%;
      display: flex;
      justify-content: center;
      align-content: center;
      &--label {
        color: #00afc7;
        margin-left: 10px;
        margin-right: 10px;
      }
      &--text {
        background-color: #000000;
        padding-left: 6px;
        border: 1px solid #15ff00;
        border-radius: 5px;
        color: #00afc7;
        font-family: 'Gemunu Libre', sans-serif;
        font-size: medium;
        -webkit-box-shadow: 0px 0px 19px -1px rgba(9, 255, 0, 0.7);
        box-shadow: 0px 0px 19px -1px rgba(0, 255, 0, 0.7);
      }
      &--search {
        padding: 3px;
        color: #00afc7;
        background-color: #000000;
        min-width: 5em;
        border: 1px solid #15ff00;

        margin-left: 5px;
        margin-right: 5px;
      }
      &--select {
        background-color: black;
        color: #00afc7;
        font-family: 'Gemunu Libre', sans-serif;
        font-size: medium;
        border: 1px solid #15ff00;
        border-radius: 5px;
        padding: 2px;
        -webkit-box-shadow: 0px 0px 19px -1px rgba(9, 255, 0, 0.7);
        box-shadow: 0px 0px 19px -1px rgba(0, 255, 0, 0.7);
      }
      ::placeholder {
        color: #00afc7;
      }
    }
    &--section {
      width: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-content: center;
      ::-webkit-scrollbar {
        width: 10px;
      }
      ::-webkit-scrollbar-track {
        box-shadow: inset 0 0 5px rgb(0, 0, 0);
        border-radius: 10px;
      }
      ::-webkit-scrollbar-thumb {
        background: #15ff00;
        border-radius: 10px;
      }
      ::-webkit-scrollbar-thumb:hover {
        background: #15ff00;
      }
      &--list {
        width: 80%;
        height: 330px;
        margin: 30px;
        padding-right: 38px;
        overflow: scroll;
        scroll-behavior: smooth;
        overflow-x: hidden;
        display: grid;
        align-self: center;
        grid-template-columns: repeat(auto-fit, minmax(186px, 1fr));
        grid-gap: 50px;
        border: #15ff00 solid 1px;
        border-radius: 5px;
        -webkit-box-shadow: 0px 0px 19px -1px rgba(255, 255, 255, 0.7);
        box-shadow: 0px 0px 19px -1px rgba(0, 255, 0, 0.7);
        scrollbar-width: 1px;
        background-image: url(../img/starsyss.gif);
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        &--item {
          background-image: url(../img/portalrickmorty.gif);
          background-position: center;
          font-size: small;
          font-family: 'Creepster', cursive;
          letter-spacing: 1px;
          -webkit-text-stroke: 1px rgb(255, 255, 255);
          text-decoration: none;
          justify-self: center;
          &--image {
            height: 150px;
            border: #ffffff solid 2px;
            border-radius: 50%;
          }
          &--name {
            margin: 5px;
            color: black;
            -webkit-text-stroke: 1px rgb(255, 255, 255);
          }
          &--species {
            margin: 5px;
            color: #000000;
            -webkit-text-stroke: 1px rgb(255, 255, 255);
          }
        }
      }
    }
  }
}
.link {
  width: 300px;
  height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  -webkit-text-stroke: 1px rgb(255, 255, 255);
  text-decoration: none;
}
